import axios from "axios";
import {
  GET_NOTIFICATIONS,
  GET_ERRORS,
  SET_LOADING_NOTIFICATIONS
} from "./types";

export const getNotifications = () => dispatch => {
  dispatch(setLoading());
  axios
    .get("/api/notifications")
    .then(res => {
      dispatch({
        type: GET_NOTIFICATIONS,
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      });
    });
};

const setLoading = () => {
  return {
    type: SET_LOADING_NOTIFICATIONS
  };
};
