import { GET_NOTIFICATIONS, SET_LOADING_NOTIFICATIONS } from "../actions/types";

const initialState = {
  notifications: [],
  isLoading: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SET_LOADING_NOTIFICATIONS:
      return {
        ...state,
        isLoading: true
      };
    case GET_NOTIFICATIONS:
      return {
        ...state,
        notifications: action.payload,
        isLoading: false
      };
    default:
      return state;
  }
}
